import React from 'react';
import { createRoot } from 'react-dom/client';
import './index.css';
import App from './App';

// Obtn el contenedor del DOM donde se montar la aplicacin
const container = document.getElementById('root');

// Crea una raz para tu aplicacin utilizando createRoot en lugar de ReactDOM.render
const root = createRoot(container);

// Renderiza la aplicacin en la raz
root.render(
  //<React.StrictMode>
    <App />
  //</React.StrictMode>
);

// Agregar el script de manejo de la animacin
window.addEventListener("load", () => {
  const chatButton = document.querySelector('[data-buildship-chat-widget-button]');
  const chatContainer = document.querySelector('#buildship-chat-widget__container');

  if (chatButton && chatContainer) {
    // Funcin para abrir el contenedor del chat
    function openChatContainer() {
      chatContainer.style.display = 'block'; // Muestra el contenedor del chat
      chatButton.classList.add('no-bounce'); // Desactiva la animacin del botn
    }

    // Funcin para cerrar el contenedor del chat
    function closeChatContainer() {
      chatContainer.style.display = 'none'; // Oculta el contenedor del chat
      chatButton.classList.remove('no-bounce'); // Reactiva la animacin del botn
    }

    // Configura el botn para alternar el estado del contenedor
    chatButton.addEventListener('click', () => {
      if (chatContainer.style.display === 'block') {
        closeChatContainer();
      } else {
        openChatContainer();
      }
    });

    // Inicializa el contenedor como oculto al cargar la pgina
    chatContainer.style.display = 'none';
  }
});
