import React from 'react';
import { Link } from 'react-router-dom';
import './css/Blog.css'; // Importa los estilos del blog desde la carpeta css dentro de components

function Blog() {
  const articulos = [
    
  ];

  return (
    <div className="blog">
      <div className="blog-intro">
        <h1>Blog</h1>
        <p className="intro">
          Bienvenido a nuestro blog, donde compartimos artculos sobre las ltimas tendencias y avances en tecnologa, digitalizacin y ms.
        </p>
        <div className="cta-seccion">
          <Link to="/servicios" className="cta-button">Mis Servicios</Link>
          <Link to="/contacto" className="cta-button">Contctame</Link>
        </div>
      </div>
      <div className="articulos-list">
        {articulos.map((articulo, index) => (
          <div key={index} className="articulo">
            <img src={articulo.imagen} alt={`Imagen de ${articulo.titulo}`} className="articulo-imagen" />
            <div className="articulo-contenido">
              <h2>{articulo.titulo}</h2>
              <div className="etiquetas">
                {articulo.etiquetas.map((etiqueta, i) => (
                  <span key={i} className="etiqueta">{etiqueta}</span>
                ))}
              </div>
              <p className="fecha">{articulo.fecha}</p>
              <p>{articulo.resumen}</p>
              <Link to={articulo.link} className="leer-mas">Leer ms</Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Blog;
