import React, { useState, useEffect } from 'react';
import './css/SobreMi.css';
import personal_story from './assets/files/profile-picture-elier2.png';
import professional from './assets/files/ecommerce_marketing.png';
import tech_vision from './assets/files/technologic_vision.png';
import { Link } from 'react-router-dom';
import { useSwipeable } from 'react-swipeable';
import { useLanguage } from '../LanguageContext'; 

function SobreMi() {
  const [currentSection, setCurrentSection] = useState(0);
  const { language } = useLanguage(); // Obtn el idioma del contexto
  const sections = {
    es: [
      {
        title: 'Historia Personal',
        image: personal_story,
        alt: 'Historia Personal',
        subtitle: 'Desde que tengo memoria, siempre he sentido una gran curiosidad por cmo funcionan las cosas.',
        content: (
          <>
            <p>
              Esa curiosidad me llev a desarmar juguetes, estudiar ingeniera, y finalmente, transformar empresas a travs de la tecnologa.
            </p>
            <p>
              Soy Elier Loya, un Ingeniero Industrial y de Sistemas con ms de 10 aos de experiencia en la direccin y optimizacin de operaciones. Mi pasin por la tecnologa y la innovacin me ha llevado a especializarme en transformacin digital y gestin logstica.
            </p>
            <p>
              A lo largo de mi carrera, he tenido el privilegio de liderar transformaciones clave en empresas como GoFarma, Farmalisto y PepsiCo, enfocndome en la digitalizacin, automatizacin y escalabilidad de procesos complejos.
            </p>
          </>
        ),
      },
      {
        title: 'Experiencia Destacada',
        image: professional,
        alt: 'Experiencia Destacada',
        subtitle: 'Mis logros reflejan mi compromiso con la excelencia operativa y mi pasin por transformar empresas.',
        content: (
          <>
            <h3>GoFarma</h3>
            <p>
              Como co-fundador y Director de Operaciones, dise e implement un modelo operativo innovador que logr una precisin de inventario del 99.8% y tiempos de entrega altamente competitivos.
            </p>
            <h3>Farmalisto</h3>
            <p>
              En mi rol como Director de Ventas en Marketplaces, lider la expansin de la presencia en lnea de la compaa, logrando un incremento del 144% en ventas y una reduccin significativa en reclamos y cancelaciones.
            </p>
          </>
        ),
      },
      {
        title: 'Visin Tecnolgica',
        image: tech_vision,
        alt: 'Visin Tecnolgica',
        subtitle: 'Creo en el poder de la tecnologa para transformar negocios y construir un futuro ms eficiente.',
        content: (
          <>
            <p>
              Hace algunos aos, tuve un momento de epifana mientras implementaba un sistema de automatizacin en una cadena logstica. Me di cuenta de que no solo estaba optimizando procesos, estaba cambiando la vida de las personas que trabajaban all, hacindoles la vida ms fcil y sus trabajos ms satisfactorios.
            </p>
            <p>
              Creo firmemente en el poder transformador de la tecnologa en los negocios. Mi enfoque se centra en aprovechar soluciones innovadoras como la IA, la automatizacin y el anlisis de datos para impulsar la eficiencia operativa y el crecimiento empresarial.
            </p>
            <p>
              Si compartes mi visin de un futuro ms eficiente y tecnolgicamente avanzado, me encantara que trabajramos juntos. Vamos a transformar tu negocio en una historia de xito.
            </p>
          </>
        ),
      },
    ],
    en: [
      {
        title: 'Personal Story',
        image: personal_story,
        alt: 'Personal Story',
        subtitle: 'Since I can remember, Ive always had a great curiosity about how things work.',
        content: (
          <>
            <p>
              This curiosity led me to disassemble toys, study engineering, and ultimately, transform companies through technology.
            </p>
            <p>
              Im Elier Loya, an Industrial and Systems Engineer with over 10 years of experience in directing and optimizing operations. My passion for technology and innovation has led me to specialize in digital transformation and logistics management.
            </p>
            <p>
              Throughout my career, Ive had the privilege of leading key transformations at companies like GoFarma, Farmalisto, and PepsiCo, focusing on digitization, automation, and scalability of complex processes.
            </p>
          </>
        ),
      },
      {
        title: 'Highlighted Experience',
        image: professional,
        alt: 'Highlighted Experience',
        subtitle: 'My achievements reflect my commitment to operational excellence and my passion for transforming businesses.',
        content: (
          <>
            <h3>GoFarma</h3>
            <p>
              As co-founder and Chief Operating Officer, I designed and implemented an innovative operational model that achieved a 99.8% inventory accuracy and highly competitive delivery times.
            </p>
            <h3>Farmalisto</h3>
            <p>
              In my role as Director of Marketplaces Sales, I led the company's online presence expansion, achieving a 144% increase in sales and a significant reduction in claims and cancellations.
            </p>
          </>
        ),
      },
      {
        title: 'Technological Vision',
        image: tech_vision,
        alt: 'Technological Vision',
        subtitle: 'I believe in the power of technology to transform businesses and build a more efficient future.',
        content: (
          <>
            <p>
              A few years ago, I had an epiphany while implementing an automation system in a logistics chain. I realized that I was not only optimizing processes; I was changing the lives of the people working there, making their lives easier and their jobs more fulfilling.
            </p>
            <p>
              I firmly believe in the transformative power of technology in business. My focus is on leveraging innovative solutions such as AI, automation, and data analysis to drive operational efficiency and business growth.
            </p>
            <p>
              If you share my vision of a more efficient and technologically advanced future, Id love to work together. Lets turn your business into a success story.
            </p>
          </>
        ),
      },
    ],
  };

  useEffect(() => {
    setCurrentSection(0); // Reinicia la seccin a 0 cuando cambie el idioma
  }, [language]);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentSection((prevSection) => (prevSection + 1) % sections[language].length);
    }, 30000); // Cambia de seccin cada 30 segundos

    return () => clearInterval(interval);
  }, [language, sections[language].length]);

  const handleSwipe = (eventData) => {
    if (eventData && eventData.dir) {
      const { dir } = eventData;
      if (dir === 'Left') {
        setCurrentSection((prevSection) => (prevSection + 1) % sections[language].length);
      } else if (dir === 'Right') {
        setCurrentSection((prevSection) => (prevSection - 1 + sections[language].length) % sections[language].length);
      }
    } else {
      console.warn('Swipe eventData is missing or does not have the dir property');
    }
  };

  const swipeHandlers = useSwipeable({
    onSwiped: handleSwipe,
    preventDefaultTouchmoveEvent: true,
    trackMouse: true,
  });

  return (
    <section id="sobre-mi" className="sobre-mi" {...swipeHandlers}>
      <div className="contenido-sobre-mi">
        <h2>{language === 'es' ? 'Sobre M' : 'About Me'}</h2>
        <div className="navigation-arrows">
          <button
            className="nav-arrow left"
            onClick={() => setCurrentSection((prevSection) => (prevSection - 1 + sections[language].length) % sections[language].length)}
          >
            &lt;
          </button>
          <button
            className="nav-arrow right"
            onClick={() => setCurrentSection((prevSection) => (prevSection + 1) % sections[language].length)}
          >
            &gt;
          </button>
        </div>
        <div className="tabs">
          {sections[language].map((section, index) => (
            <button
              key={index}
              className={`tab-button ${currentSection === index ? 'active' : ''}`}
              onClick={() => setCurrentSection(index)}
            >
              {section.title}
            </button>
          ))}
        </div>
        <div className="section-content">
          <div className="sub-title-container shinyy">{sections[language][currentSection].subtitle}</div>
          <div className="image-container">
            <img src={sections[language][currentSection].image} alt={sections[language][currentSection].alt} />
          </div>
          <div className="text-content">
            {sections[language][currentSection].content}
          </div>
        </div>
        <div className="cta-seccion-sobre-mi">
          <Link to="/servicios" className="cta-button-2">{language === 'es' ? 'Mis Servicios' : 'My Services'}</Link>
          <Link to="/contacto" className="cta-button-2">{language === 'es' ? 'Contctame' : 'Contact Me'}</Link>
        </div>
        <div className="horizontal-scroll-indicator">
          {sections[language].map((_, index) => (
            <div
              key={index}
              className={`indicator ${currentSection === index ? 'active-indicator' : ''}`}
              onClick={() => setCurrentSection(index)}
            />
          ))}
        </div>
      </div>
    </section>
  );
}

export default SobreMi;